import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import { rhythm } from '../utils/typography'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/nav-bar'
import Footer from '../components/footer'
import PostList from '../components/post-list'

const Title = styled.h1`
  font-family: 'Roboto';
  font-size: 2.5rem;
  font-weight: 400;
  line-height: ${rhythm(2)};
  margin-top: ${rhythm(2)};
`

const TagCount = styled.p`
  font-family: 'Roboto';
  font-size: 1.5rem;
  font-weight: 100;
`

const ShowAllTags = styled.span`
  font-family: 'Roboto';
  font-weight: 100;
`

class TagRoute extends React.Component {
  render() {
    const posts = this.props.data.allMarkdownRemark.edges
    const siteMetadata = this.props.data.site.siteMetadata
    const title = `${this.props.pathContext.tag} 태그 · ${siteMetadata.title}`

    return (
      <PageWrapper>
        <Helmet
          title={title}
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}${this.props.location.pathname}`,
            },
          ]}
          meta={[
            { name: 'keywords', content: this.props.pathContext.tag },
            {
              property: 'og:url',
              content: `${siteMetadata.url}${this.props.location.pathname}`,
            },
            { property: 'og:title', content: title },
          ]}
        />
        <NavBar />
        <PostContentWrapper>
          <Title>{this.props.pathContext.tag}</Title>
          <TagCount>
            {this.props.data.allMarkdownRemark.totalCount}개의 글
          </TagCount>
          <PostList posts={posts} />
          <ShowAllTags>
            <Link to="/tags/">모든 태그 보기</Link>
          </ShowAllTags>
        </PostContentWrapper>
        <Footer />
      </PageWrapper>
    )
  }
}

export default TagRoute

export const pageQuery = graphql`
  query TagPage($tag: String) {
    site {
      siteMetadata {
        title
        url
      }
    }
    allMarkdownRemark(
      limit: 1000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            date
            mainImage
          }
        }
      }
    }
  }
`



